import React, { useState ,useEffect } from 'react';
import { Route, Switch } from "react-router-dom";
import Footer from './components/Footer';
import Header from './components/Header';
import TopPage from './components/TopPage';
import Aboutus from './components/Aboutus';
import Contact from './components/Contact';
import Library from './components/Library';
import Works from './components/Works';
import './App.css';

function App(){
  const [pageNumber, setPageNumber] = useState(0);

  const checkPage = () => {
    if(window.location.pathname === '/'){
      setPageNumber(0);
    } else if(window.location.pathname === '/Aboutus'){
      setPageNumber(1);
    } else if(window.location.pathname === '/Library' | window.location.pathname === '/Icons' | window.location.pathname === '/Others'){
      setPageNumber(2);
    } else if(window.location.pathname === '/Contact'){
      setPageNumber(3);
    } else if(window.location.pathname === '/Works')  {
      setPageNumber(4);
    }
  }

  useEffect(() => {
    checkPage();
    window.onpopstate = checkPage;
  },[])

  return(
    <>
        <Header
            pageNumber = {pageNumber}
            setPageNumber0 = {() => setPageNumber(0)}
            setPageNumber1 = {() => setPageNumber(1)}
            setPageNumber2 = {() => setPageNumber(2)}
            setPageNumber3 = {() => setPageNumber(3)}
            setPageNumber4 = {() => setPageNumber(4)}
          />
        <div className="mainContents">
          <Switch>
            <Route path="/" exact>
              <TopPage
                setPageNumber1 = {() => setPageNumber(1)}
                setPageNumber2 = {() => setPageNumber(2)}
                setPageNumber3 = {() => setPageNumber(3)}
                setPageNumber4 = {() => setPageNumber(4)}
              />
            </Route>
            <Route path="/Aboutus">
              <Aboutus />
            </Route>
            <Route exact path="/Library">
              <Library
                pageNumber = {pageNumber}
                setPageNumber2 = {() => setPageNumber(2)}
              />
            </Route>
            <Route path="/Contact">
              <Contact />
            </Route>
            <Route path="/Works">
              <Works
                setPageNumber0 = {() => setPageNumber(0)}
              />
            </Route>
          </Switch>
        </div>
        <Footer
          pageNumber = {pageNumber}
        />
    </>
  )
}

export default App;